	module.exports = {"data":{"markdownRemark":{"html":"<h3>AKD Softwoods - Datalogger</h3>\n<h4>Project Description</h4>\n<p>Our client, AKD Softwoods uses Programmable Logic Controllers (PLCs) in their machinery, which captures and holds valuable information about the state of the machinery. This information is valuable because it can enable engineers to diagnose faults and inefficiencies.</p>\n<p>Our project aim was to deliver a system that could add connections to PLCs and set data points (tags) to log at either a defined time interval, or on a \"change\" event (i.e. when the data point changes from the previous value).</p>\n<p>The primary goal of this system is to be able to accept timeseries data in as close to realtime as possible, and make that data available via a web user interface.</p>\n<h4>Project Deliverable</h4>\n<p>The requirement to be able to accept data from a range of PLC hardware meant that the system design had to be flexible. This was accomplished by using a common driver interface, that defines ingress points and data communication protocols. The data is fed into a message queue, which is primarily to allow the influxDB instance to pull the data for persistence. The message queue approach also allows the UI to connect to the data stream for realtime charting.</p>\n<p>The administration interface is a typical client-server architecture backed by a RESTful API. The client is a Single Page Application written from scratch primarily using React, a modern view framework for JavaScript.</p>\n<p>Because the system is designed with a microservice approach, each piece is containerized and orchestrated using docker-compose.</p>\n<h4>Project Team Structure</h4>\n<p><strong>Divided into three groups:</strong></p>\n<ul>\n<li>Frontend</li>\n<li>Backend</li>\n<li>Testing/Documentation</li>\n</ul>\n<h4>My Role</h4>\n<ul>\n<li>Frontend Team Lead</li>\n<li>Frontend / UI Development</li>\n<li>Containerization with Docker</li>\n<li>Project Management - issues and task boards</li>\n<li>Report writing and review</li>\n</ul>\n<h4>My Key Deliverables</h4>\n<ul>\n<li>Develop UI according to requirements and wireframes</li>\n<li>Technology stack</li>\n<li>Define project structure</li>\n<li>Implementation of UI elements, async communications to the RESTful API, realtime graph components</li>\n</ul>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/deakin-portfolio/static/akd-sources-tags-fa4bfbc67277ee360edd4f62363a2602-5190d.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; padding:8px;box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.3);margin-left:0 !important;; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 103.21469575200919%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Image showing the source and tags administration interface\"\n        title=\"\"\n        src=\"/deakin-portfolio/static/akd-sources-tags-fa4bfbc67277ee360edd4f62363a2602-72f05.png\"\n        srcset=\"/deakin-portfolio/static/akd-sources-tags-fa4bfbc67277ee360edd4f62363a2602-55432.png 148w,\n/deakin-portfolio/static/akd-sources-tags-fa4bfbc67277ee360edd4f62363a2602-a1930.png 295w,\n/deakin-portfolio/static/akd-sources-tags-fa4bfbc67277ee360edd4f62363a2602-72f05.png 590w,\n/deakin-portfolio/static/akd-sources-tags-fa4bfbc67277ee360edd4f62363a2602-5190d.png 871w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \n<div><iframe src=\"https://www.youtube.com/embed/muaVpYR2fVM\" width=\"590\" height=\"332\"></iframe></div></p>","frontmatter":{"date":"May 31, 2018","path":"/project","title":"Projects - AKD Softwoods"}}},"pathContext":{}}

